























































import { AppService } from "@/service/app.service";
import PostTitleMeta from "@/x-vue/components/forum/post/PostTitleMeta.vue";
import { Vue, Component, Watch } from "vue-property-decorator";

import AdvertisementSquareBanners from "@/x-vue/widgets/advertisement/AdvertisementSquareBanners.vue";
import AdvertisementLineBanner from "@/x-vue/widgets/advertisement/AdvertisementLineBanner.vue";

import PushNotificationIcon from "@/x-vue/components/push-notification/push-notification-icon.vue";
import { ForumInterface, PostModel } from "@/x-vue/interfaces/forum.interface";
import { Err } from "@/x-vue/services/defines";

@Component({
  components: {
    PostTitleMeta,
    AdvertisementSquareBanners,
    AdvertisementLineBanner,
    PushNotificationIcon,
  },
})
export default class PostList extends Vue {
  app = AppService.instance;

  forum: ForumInterface = new ForumInterface();

  total = 0;
  noOfPages = 10;
  currentPage = "1";

  async mounted(): Promise<void> {
    this.init();
    this.initPostCount();
    this.loadPosts();
  }

  get categoryId(): string {
    return this.$route.params.categoryId;
  }

  get subCategory(): string {
    return this.$route.query.sc as string;
  }

  get posts(): PostModel[] {
    return this.forum.posts;
  }

  get createLink(): string {
    const page = this.$route.query.page ? parseInt(this.$route.query.page as string) : 1;
    return "/forum/edit/" + this.linkGen(page);
  }

  linkGen(pageNum: number): string {
    const parts: string[] = [];
    if (pageNum > 1) parts.push(`page=${pageNum}`);
    if (this.subCategory) {
      parts.push("sc=" + this.subCategory);
    }
    let uri: string = this.categoryId;
    if (parts.length) {
      uri += "?" + parts.join("&");
    }
    return uri;
  }

  onPageChanged(page: number): void {
    // console.log("page changed", page);
    this.forum.page = page;
    this.loadPosts();
  }

  init(): void {
    this.forum.categoryId = this.$route.params.categoryId;
    const uIdx = this.$route.params.userIdx;
    if (uIdx) this.forum.userIdx = parseInt(uIdx);
    this.forum.comments = 1;

    if (this.$route.query.page) {
      this.currentPage = this.$route.query.page as string;
      this.forum.page = parseInt(this.currentPage);
    } else {
      this.currentPage = "1";
      this.forum.page = 1;
    }

    if (this.subCategory) {
      this.forum.subCategory = this.subCategory;
    }
    // console.log("init::searchOptions", this.forum.searchOptions);
  }

  async initPostCount(): Promise<void> {
    try {
      this.total = await this.app.api.postCount(this.forum.searchOptions);
      this.noOfPages = Math.ceil(this.total / this.forum.limit);
    } catch (e) {
      if (e == Err.category_not_exists) {
        console.log("@ignore category not exists error on counting no of posts.");
      } else {
        this.app.error(e);
      }
    }
  }

  @Watch("$route.query.sc")
  onQueryChange(subcategory: string, oldsubcategory: string): void {
    // return on the first load. oldsubcategory is undefine on the first load
    console.log(subcategory, oldsubcategory);
    this.initPostCount();
    this.loadPosts();
  }

  async loadPosts(): Promise<void> {
    // console.log(this.forum.searchOptions);
    if (this.forum.canLoad) return;
    this.forum.loading = true;
    try {
      this.forum.posts = await this.app.api.postSearch(this.forum.searchOptions);
      // console.log(this.forum.posts);
    } catch (error) {
      this.app.error(error);
    }
    this.forum.endLoad();
  }
}
